// Color
$basic-color: #ac9455;
$black: #141414;
$gray: #a3a3a3;
$nav-black: #010101;
$xs-nav-black: rgba(20, 20, 20, 0.95);

// Font
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://brick.a.ssl.fastly.net/Montserrat:400,700,900);

$open-sans: "OpenSans";
$mont: "Montserrat";

// nav icon
@mixin navicon_lines {
  display: inline-block;
  width: 15px;
  height: 1px;
  background: $basic-color;
  border-radius: 1px;
  transition: .3s ease; }
